
.diagramExample {

    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;
    cursor: default;
    user-select: none;
    border-top: 1px solid lightgray;


    .analysisAllT {
        position: absolute;
        width: 100%;
        height: 60%;
        z-index: 1;
        bottom: 0;
        background: #FFF;
        overflow: hidden;

        .changeHeight {
            width: 100%;
            cursor: n-resize;

            .changeEvent {
                width: 100%;
                border: 1px solid lightgray
            }

            .changeFFF {
                width: 100%;
                border: 3px solid #FFF
            }
        }

        .right-a-tool {

            display: flex;
            flex-direction: row;
            align-items: center;

            .tag {
                margin-left: 35px;

                .el-tag {
                    cursor: pointer !important;
                    margin-left: 5px !important;
                }
            }

            .right-tools {
                display: flex;
                flex-direction: row-reverse;
                right: 10px;
                position: absolute;
            }

            .itools {
                display: flex;
                margin: 10px 10px 0 0;

                .tool-icons {
                    margin-right: 20px;
                    cursor: pointer;

                    .tool-icon {
                        width: 1.5em;
                        height: 1.5em;
                        vertical-align: -0.15em;
                        fill: currentColor;
                        overflow: hidden;
                    }
                }

            }
        }

        .allWarnList {

            .el-table__body-wrapper {
                height: 100% !important;
            }

            .el-table__body-wrapper is-scrolling-none {
                height: 100% !important;
            }


            padding: 8px 40px 20px 40px;
            overflow: auto;
            height: 100%;

            .analysisResult {
                flex: 1;
                overflow: hidden;
                height: 100%;
            }
        }
    }


    .Meng {
        position: absolute;
        overflow: hidden;
        z-index: 1;
        background-color: #000000;
        color: rgb(44, 62, 80);
        height: 100%;
        width: 100%;
        top: 0;
        left: 0;
        opacity: 0.5;
    }

    .warn-node-list {
        margin-top: 20px;
    }

    /deep/ .v-modal {
        position: absolute;
        height: 100%;
        overflow: hidden;
        z-index: 0 !important;
    }

    /deep/ .el-drawer__header {
        line-height: 80px;
        text-align: left;
        font-size: 18px;
        font-weight: 600;
        color: #3f3e3d;
        margin-bottom: 2px;
        padding: 0 20px;
    }

    /deep/ .left-drawer {
        width: 30% !important;
    }

    /deep/ .right-drawer {
        width: 30% !important;
    }


    ::-webkit-scrollbar-thumb {
        height: 0;
        width: 0;
        color: transparent;
        background: transparent;
    }

    /deep/ .el-drawer.btt {
        width: 100%;
        left: 0;
        right: 0;
        height: 45% !important;
    }

    .left-drawer {
        .property-drawer__content {
            display: flex;
            flex-direction: column;
            flex-grow: 1;
            padding: 0 20px;

            .categories {
                display: flex;
                flex-direction: column;
            }

            .el-autocomplete {
                display: flex;
            }


            .connector {
                display: flex;
                flex-grow: 1;
                font-size: 18px;
                font-weight: 600;
                margin: 12px 0;
                align-items: center;
                text-align: left;

                .connector-title {
                    flex-grow: 1;
                }
            }

            .connector-entry {
                .connector-input {
                    width: 100%;
                    border: 0;
                    text-align: center;
                    font-size: 14px;
                }

                /deep/ .el-table.cell {
                    padding-left: 0 !important;
                    padding-right: 0 !important;
                }

                /deep/ .el-input__inner {
                    padding: 0 !important;
                    text-align: center !important;
                }

                /deep/ .el-textarea__inner {
                    padding: 0 4px !important;
                    padding-right: 0 !important;
                }
            }

            .el-form {
                display: flex;
                flex-direction: column;
                flex-grow: 1;
            }

            .connector-tool {
                right: 20px;
                display: flex;

                .el-button {
                    margin-left: 0px !important;
                    border: transparent;
                    color: #409EFF;
                }
            }
        }

        .demo-drawer__footer {
            margin-bottom: 20px;
        }

        .resource-title {
            font-weight: 400;
            text-align: left;
            vertical-align: middle;
            float: left;
            font-size: 15px;
            color: rgb(51, 51, 51);
            line-height: 40px;
            -webkit-box-sizing: border-box;
            box-sizing: border-box;

            .el-form-item {
                flex-grow: 1;
                margin-bottom: 6px;
            }


        }
    }


    .left-tool-bar {
        display: flex;
        /* width: 500px; */
        position: absolute;
        left: 20px;
        top: 15%;

        .left-toolbar {
            display: flex;
            top: 20px;
            position: absolute;

            .toolbar {
                font-size: 24px;
                box-shadow: 3px 3px 1px 1px #ddd;
                background: #fff;
                border-radius: 4px;
                padding: 5px;

                .tool {
                    padding: 10px 4px;
                    cursor: pointer;
                }

                .tool:hover {
                    border-radius: 1%;
                    background: rgba(59, 113, 233, 0.12);
                }
            }
        }
    }


    .right-toolbar {
        display: flex;
        top: 20px;
        position: absolute;
        left: -400px;
        width: 100px;

        .toolbar {
            display: flex;
            font-size: 24px;
            box-shadow: 3px 3px 1px 1px #ddd;
            background: #fff;
            border-radius: 4px;
            padding: 5px;

            .tool {
                /*border-right: 1px solid #ccc;*/
                padding: 0 10px;
                cursor: pointer;
            }

            .active-tool {
                padding: 0 10px;
                cursor: pointer;
                background: rgba(59, 113, 233, 0.12);
                border-radius: 4px;
            }

            .tool:hover {
                border-radius: 1%;
                background: rgba(59, 113, 233, 0.12);
            }

            .tool-scale-number {
                /*border-right: 1px solid #ccc;*/
                padding: 0 3px;
                color: #3B71E9;
            }

            .tool-scale-op {
                /*border-right: 1px solid #ccc;*/
                padding: 0 3px;
                cursor: pointer;


            }

            .tool-scale-op:hover {
                border-radius: 1%;
                background: rgba(59, 113, 233, 0.12);
            }

            .tool-scale {
                display: flex;
                font-size: 16px;
                align-items: center;
            }
        }
    }

    .right-form {
        /*width: 500px;*/
        height: 100%;
        border-left: 1px solid #ccc;
        background: #fff;

        ::-webkit-scrollbar-thumb {
            height: 0;
            width: 0;
            color: transparent;
            background: transparent;
        }

        .edit-form {
            height: 100%;
            display: flex;
            flex-direction: column;

            .edit-total {
                flex-grow: 1;
                display: flex;
                flex-direction: column;
                padding: 0 20px;;

                .el-form {
                    display: flex;
                    flex-direction: column;
                    margin-top: 12px
                }
            }

            .categories {
                text-align: left;
                display: flex;
                flex-direction: column;

                .el-cascader {
                    flex-grow: 1;
                    margin-bottom: 12px;
                }
            }


            .header-item-inner-long {
                flex-grow: 1;
                margin-bottom: 12px;
            }

            .clickButton {
                display: flex;
                justify-content: center;
                align-items: center;
            }


            .resource-selected {
                display: flex;
                flex-direction: column;

                .resource-tool {
                    margin-bottom: 8px;

                    .resource-tool-edit {
                        position: absolute;
                        right: 20px;
                        display: flex;

                        .el-button {
                            margin-left: 0px !important;
                            border: transparent;
                            color: #409EFF;
                        }
                    }
                }
            }

            .resource-title {
                font-weight: 400;
                text-align: left;
                vertical-align: middle;
                float: left;
                font-size: 15px;
                color: rgb(51, 51, 51);
                line-height: 40px;
                -webkit-box-sizing: border-box;
                box-sizing: border-box;

                .el-form-item {
                    flex-grow: 1;
                    margin-bottom: 12px;
                }
            }


        }

        .title {
            line-height: 80px;
            text-align: left;
            font-size: 18px;
            font-weight: 600;
            color: rgb(63, 62, 61);
            padding-left: 20px;
        }

        .connector-title {
            line-height: 80px;
            text-align: left;
            font-size: 20px;
            font-weight: 600;
            color: rgb(63, 62, 61);
        }
    }

    .head-title {
        border-bottom: 1px solid lightgray;
        display: flex;
        flex-direction: row;
        align-items: center;

        .resource_type {
            margin-left: 20px;
            font-size: 16px;
            /* color: blue; */
            /* align-items: center; */
            border-color: transparent;
            color: lightslategrey;
            background: 0 0;
            padding-left: 0;
            padding-right: 0;
            height: 100%;
            align-items: center;
            display: flex;
            line-height: 80px;
            font-weight: 600;
        }

        .head-change-resourceType {
            margin-left: 20px;
            font-size: 10px;
            color: blue;
            cursor: pointer;

            .changeResource {
                width: 2em;
                height: 2em;
            }
        }

        .title {
            line-height: 80px;
            text-align: left;
            font-size: 18px;
            font-weight: 600;
            color: rgb(63, 62, 61);
            padding-left: 20px;
        }
    }


    .head-left-title {
        border-bottom: 1px solid lightgray;
        display: flex;
        flex-direction: row;
        align-items: center;

        .loading {
            margin-right: 20px;
            font-size: 10px;
            color: blue;
        }

        .title {
            line-height: 80px;
            text-align: left;
            font-size: 18px;
            font-weight: 600;
            color: rgb(63, 62, 61);
            padding-left: 20px;
            flex-grow: 1;
        }
    }
}


/deep/ .el-drawer__wrapper {
    z-index: 500 !important;
}

/deep/ .el-form-item__content {
    margin-left: 0px !important;;
}

/deep/ .all_warn_table {
    font-size: 0;
}

/deep/ .all_warn_table label {
    width: 200px;
}

/deep/ .all_warn_table .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    margin-left: 50px;
    width: 100%;
}

.eloption {
    .el-scrollbar__wrap {
        overflow: visible; //超出部分不滚动，直接显示
    }

    .el-scrollbar__thumb {
        display: none; //去掉右侧滚动条
    }

    ::-webkit-scrollbar-thumb {
        height: 0;
        width: 0;
        color: transparent;
        background: transparent;
    }

    .el-select-dropdown__item {
        height: auto;
        display: flex;
        flex-direction: row;
        line-height: 30px;
    }
}

.normal-svg {
    cursor: default;
    background-color: #F0F6FB !important;
    overflow: auto;
    /* border: 1px solid lightgray;*/
    border-radius: 3px;
    background-image: linear-gradient(90deg, #eee 10%, rgba(0, 0, 0, 0) 10%), linear-gradient(#eee 10%, rgba(0, 0, 0, 0) 10%);
    background-size: 10px 10px;
    margin-right: 200px;
    height: 100%;
}

.grab-svg {
    cursor: grab;
    background-color: #F0F6FB !important;
    overflow: auto;
    /* border: 1px solid lightgray;*/
    border-radius: 3px;
    background-image: linear-gradient(90deg, #eee 10%, rgba(0, 0, 0, 0) 10%), linear-gradient(#eee 10%, rgba(0, 0, 0, 0) 10%);
    background-size: 10px 10px;
    margin-right: 200px;
    height: 100%;
}

.grab-svg:active {
    cursor: grabbing;
}

.body {
    margin: 0;
}

#isc_dataflow_editor {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    height: 100%;
    width: 100%;
}

.el-loading-mask.is-fullscreen {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}

.foreignObject {
    /* 解决foreignObject默认遮挡溢出部分导致的形变 */
    overflow: inherit;
    animation-duration: 1s;
    animation-fill-mode: both;
    animation-name: fadeInLeft;
}

.side {

    position: absolute;
    width: 0;
    height: 0;
    margin-top: 15%;
    left: 3px;

    z-index: 3;
    cursor: pointer;
    /* background-color: #fff; */

}

.right-tool-bar {
    display: flex;
    /* width: 500px; */
    position: absolute;
    height: 100%;
    right: 0;
    top: 0;
    /* border-left: 1px solid #ccc; */
    /* background: #fff; */
    flex-direction: row;
}


.selected-type .all_resources .resource-each .resources:hover {
    transform: translateY(-4px);
    background: #F5F5F5;
    border-radius: 8px;
    box-shadow: 0 6px 24px rgba(38, 38, 38, .06);

}

.selected-type .groups .resources_group:hover {

    background: #F5F5F5;
    border-radius: 2px;

}


.selected-type .groups .resources_group:hover {
    color: #262626;
}

.selected-type {
    display: flex;
    /*padding-top: 20px;*/
    height: 100%;

    .groups {
        display: inline-block;
        vertical-align: top;
        flex-shrink: 0;
        margin-top: 10px;
        line-height: 32px;
        width: 100px;
        max-height: 100%;
        border-right: 1px solid var(--color-brand3-2, #e9edf0);

        .resources_group {
            padding: 0 15px 2px 15px;
            font-size: 14px;
            font-weight: 400;
            text-align: left;
            color: #8c8c8c;
            cursor: pointer;
        }
    }

    .all_resources {
        overflow-y: auto;
        display: flex;
        flex-wrap: wrap;
        padding: 0 20px 14px;
        width: 100%;

        .resource-each {
            text-align: left;

            .resource-title {
                color: var(--color-text1-7, #8b8b8b);
                /* color: #8b8b8b; */
                font-size: 14px;
                margin: 10px 10px 10px 15px;
            }

            .resources {
                /* display: flex;*/
                width: 150px;
                height: 80px;
                text-align: left;
                background-color: hsla(0, 0%, 100%, 0.9);
                /*transition: background-color 0.2s;*/
                word-break: break-all;
                align-items: center;
                cursor: pointer;
                border: 1px solid var(--color-brand3-2, #e9edf0);
                border-radius: 8px;
                margin: 10px 10px 10px 15px;
                display: inline-flex;
                -webkit-box-align: center;
                transition: transform .218s ease-in, -webkit-transform .218s ease-in;

                .resource-icon {
                    padding-left: 4px;
                }

                .icon {
                    font-size: 24px;
                }

                .resource-label {
                    padding-left: 5px;

                    .resource {
                        font-size: 14px;
                        font-weight: 600;
                    }

                    .desc {
                        font-size: 8px;
                        color: #999;
                    }
                }

            }
        }

    }
}

