.nodesBus-contain {
  box-sizing: border-box;
  width: 10px;
  height: 10px;
  position: absolute;
}
.nodesBus {
  box-sizing: border-box;
  transform: translate(-200px, -80px);
  width: 200px;
  height: 80px;
  background-color: rgba(255, 255, 255, 0.9);
  border: 1px solid #289de9;
  border-radius: 15px;
  font-size: 12px;
  -webkit-transition: background-color 0.2s;
  transition: background-color 0.2s;
  position: absolute;
  pointer-events: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.nodesBus .icon {
  width: 26px;
  height: 26px;
  margin: 1px;
  border-radius: 100%;
  float: left;
  color: #fff;
  font-size: 16px;
  background-color: #289de9;
  cursor: pointer;
}
.nodesBus .name {
  margin-left: 2px;
  width: 135px;
  height: 28px;
  line-height: 28px;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
  user-select: none;
}
