.pane-node-content {
  /* box-sizing: border-box;*/
  width: 200px;
  height: 80px;
  background-color: hsla(0, 0%, 100%, 0.9);
  border: 1px solid #289de9;
  border-radius: 15px;
  font-size: 12px;
  -webkit-transition: background-color 0.2s;
  transition: background-color 0.2s;
  word-break: break-all;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.pane-node-content .icon {
  width: 40px;
  height: 40px;
  margin: 0 2px 0 6px;
  color: #fff;
  font-size: 200%;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.pane-node-content .warn-icon {
  display: flex;
  width: 18px;
  height: 18px;
  position: absolute;
  right: 10px;
  top: 10px;
}
.pane-node-content .state-icon {
  display: flex;
  width: 18px;
  height: 18px;
  position: absolute;
  right: 3px;
  top: 3px;
}
.pane-node-content .isStart {
  display: flex;
  width: 18px;
  height: 18px;
  position: absolute;
  right: 3px;
  bottom: 3px;
}
.pane-node-content .parentLink {
  font-size: 0;
  height: 12px;
  width: 12px;
  position: absolute;
  top: 0;
  left: 90px;
  transform: translateX(-50%);
  border-top: 6px solid black;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
}
.pane-node-content .childLink {
  height: 10px;
  width: 10px;
  position: absolute;
  bottom: 0;
  left: 90px;
  transform: translate(-50%, 50%);
  border-radius: 50%;
  background: #ffffff;
  cursor: crosshair;
}
.pane-node-content .name {
  float: left;
  /* margin-left: 2px; */
  /* width: 160px; */
  /* line-height: 120%; */
  font-size: 12px;
  text-overflow: ellipsis;
  white-space: normal;
  cursor: pointer;
  user-select: none;
  /* height: 80px; */
  background: transparent;
  border: none;
  text-align: left;
  /* padding: 4px; */
  display: flex;
  /* justify-content: center; */
  align-items: center;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  margin-top: 4px;
}
.pane-node-parent-hl {
  position: fixed;
  top: -5px;
  height: 10px;
  width: 100%;
  display: flex;
  transform: translateX(6px);
}
.pane-node-parent-hl .space {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  border: 1px solid gray;
  background: #ffffff;
  position: absolute;
  right: 0;
  top: 0;
  cursor: crosshair;
}
.pane-node-parent-hl .space:hover {
  box-shadow: 0 0 0 6px #3ddd73;
}
.pane-node-parent-hl > div {
  position: relative;
  display: inline-block;
}
.paneSuccess {
  background: #3ddd73 !important;
}
#parent-cross {
  top: 0px;
  height: 100%;
  width: 10px;
  left: -13px;
}
#parent-cross .space {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
#parent-cross .to-move-space {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
#children-cross {
  top: 0px;
  right: 0;
  height: 100%;
  width: 10px;
  position: fixed;
}
#children-cross .space {
  position: absolute;
  left: -3px;
  top: 49%;
  transform: translateY(-50%);
}
#children-cross .to-move-space {
  position: absolute;
  left: -3px;
  top: 49%;
  transform: translateY(-50%);
}
#down-cross {
  bottom: -3px;
  left: -4px;
  width: 100%;
  height: 10px;
  position: fixed;
}
#down-cross .space {
  position: absolute;
  bottom: -3px;
  left: 49%;
  transform: translateX(-50%);
}
#down-cross .to-move-space {
  position: absolute;
  bottom: -3px;
  left: 49%;
  transform: translateX(-50%);
}
#up-cross {
  top: -3px;
  left: -4px;
  width: 100%;
  height: 10px;
  position: fixed;
}
#up-cross .space {
  position: absolute;
  top: -3px;
  left: 49%;
  transform: translateX(-50%);
}
#up-cross .to-move-space {
  position: absolute;
  top: -3px;
  left: 49%;
  transform: translateX(-50%);
}
.pane-node-up {
  position: fixed;
  bottom: 0;
  width: 100%;
  display: flex;
  opacity: 0;
  transform: translateX(6px);
}
.pane-node-up .space {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  border: 1px solid gray;
  background: #ffffff;
  position: absolute;
  right: 0px;
  bottom: -6px;
  cursor: crosshair;
}
.pane-node-up .to-move-space {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  border: 1px solid gray;
  background: #ffffff;
  position: absolute;
  right: 0px;
  bottom: -6px;
  cursor: move;
}
.pane-node-up .space {
  background: #FFFFFF;
}
.pane-node-hidden {
  opacity: 0 !important;
}
.pane-node-up:hover {
  opacity: 1;
}
