
.dragFrame {
  z-index: -10;
}
.dragFrame .dragFrameArea {
  width: 200px;
  height: 80px;
  background-color: hsla(0, 0%, 100%, 0.9);
  border: 1px dashed black;
  border-radius: 15px;
  font-size: 12px;
  -webkit-transition: background-color 0.2s;
  transition: background-color 0.2s;
  box-sizing: border-box;
}
