.connector {
  stroke: hsla(0, 0%, 50%, 0.6);
  stroke-width: 2px;
  fill: none;
}
.menu_cover {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}
.connector-hl {
  stroke: hsla(0, 0%, 50%, 0.4);
  stroke-width: 5px;
  fill: none;
}
.menu_contain {
  width: 80px;
  border: 1px solid rgba(1, 1, 1, 0.3);
  background: #ffffff;
  border-radius: 5px;
  padding: 4px;
  cursor: pointer;
  /* padding-left: 10px; */
  text-align: center;
  display: flex;
  /* flex-grow: 1; */
  flex-direction: column;
  align-items: center;
}
.menu_contain span {
  width: 100%;
  display: inline-block;
  cursor: pointer;
  font-size: 14px;
  padding-bottom: 2px;
}
.menu_contain span:hover {
  background-color: rgba(40, 157, 233, 0.3);
  cursor: pointer;
}
