}

#children-cross .space {
    position: absolute;
    left: -3px;
    top: 49%;
    transform: translateY(-50%);
}

#children-cross .to-move-space {
    position: absolute;
    left: -3px;
    top: 49%;
    transform: translateY(-50%);
}

