.pane-node-up {
    position: fixed;
    bottom: 0;
    width: 100%;
    display: flex;
    opacity: 0;
    transform: translateX(6px);
}

.pane-node-up .space {
    width: 14px;
    height: 14px;
    border-radius: 50%;
    border: 1px solid gray;
    background: #ffffff;
    position: absolute;
    right: 0px;
    bottom: -6px;
    cursor: crosshair;
}

.pane-node-up .to-move-space {
    width: 14px;
    height: 14px;
    border-radius: 50%;
    border: 1px solid gray;
    background: #ffffff;
    position: absolute;
    right: 0px;
    bottom: -6px;
    cursor: move;
}

.pane-node-up .space {
    background: #FFFFFF;
}

.pane-node-hidden {
    opacity: 0 !important;
}

.pane-node-up:hover {
    opacity: 1;
}

