.node-pop {
    position: absolute;
    right: -10px;
    top: -70px;
    border: 1px solid #ccc;
    padding: 10px 20px;
    border-radius: 30px 20px;
    background: #fff;
    pointer-events: none;
    display: none;
}

.node-pop:after {
    content: '.';
    font-size: 0;
    height: 20px;
    width: 20px;
    background: #fff;
    border: 2px #ccc solid;
    border-top: none;
    border-right: none;
    z-index: 100;
    position: absolute;
    transform: rotate(-34deg) skew(-33deg, -1deg) scale(1.5);
    border-radius: 20px 0 0 0;
    left: -14px;
    top: 22px;
    pointer-events: none;
    display: none;
}

