
.only-watch-el {
    pointer-events: none;
}

.connector-success {
    stroke: #00FF00;
    stroke-width: 2px;
    /* fill: none; */
    fill: transparent;
    cursor: pointer;
    stroke-dasharray: 100%;
    animation: line_success 2s;
}

.connector-error-success {
    stroke: rgb(255, 0, 0);
    stroke-width: 2px;
    /* fill: none; */
    fill: transparent;
    cursor: pointer;
    stroke-dasharray: 100%;
    animation: line_success 2s;
}

@keyframes line_success {
    0% {
        stroke-dashoffset: 100%;
    }
    100% {
        stroke-dashoffset: 0%;
    }
}

.connector-active {
    stroke: rgba(91, 230, 20, 0.6);
    /* fill: none; */
    fill: transparent;
    cursor: pointer;
    stroke-width: 2px;
    stroke-dasharray: 5px;
    stroke-dashoffset: 1000px;
    animation: grown 40s infinite linear;
}

.connector-error-active {
    stroke: rgba(255, 0, 0, 0.6);
    /* fill: none; */
    fill: transparent;
    cursor: pointer;
    stroke-width: 2px;
    stroke-dasharray: 5px;
    stroke-dashoffset: 1000px;
    animation: grown 40s infinite linear;
}

.defaultArrow {
    stroke: #00c0ff;
    stroke-width: 2px;
    /* fill: none; */
    fill: transparent;
    cursor: pointer;
    /*stroke-dasharray: 5px;*/
    stroke-dashoffset: 1000px;
    animation: grown 40s infinite linear;
}

.defaultErrorArrow {
    stroke: rgb(255, 99, 71);
    stroke-width: 2px;
    /* fill: none; */
    fill: transparent;
    cursor: pointer;
    /*stroke-dasharray: 5px;*/
    stroke-dashoffset: 1000px;
    animation: grown 40s infinite linear;
}

@keyframes grown {
    to {
        stroke-dashoffset: 0px;
    }
}

.menu_cover {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
}

.connector-hl {
    stroke: #00c0ff;
    stroke-width: 5px;
    /* fill: none; */
    fill: transparent;
    cursor: pointer;
}

.connector-error-hl {
    stroke: #ff4d51;
    stroke-width: 5px;
    /* fill: none; */
    fill: transparent;
    cursor: pointer;
}

.connector-hl-a {
    stroke: rgba(91, 230, 20, 0.6);
    stroke-width: 5px;
    /* fill: none; */
    fill: transparent;
    cursor: pointer;
}

.connector-hl-error-a {
    stroke: rgba(255, 0, 0, 0.6);
    stroke-width: 5px;
    /* fill: none; */
    fill: transparent;
    cursor: pointer;
}

.menu_contain {
    width: 90px;
    border: 1px solid rgba(1, 1, 1, 0.3);
    background: #ffffff;
    border-radius: 5px;
    cursor: pointer;
    text-align: center;
    font-size: 14px;
    line-height: 22px;
}

.menu_contain span {
    width: 100%;
    display: inline-block;
}

.menu_contain div:hover {
    background-color: rgba(40, 157, 233, .3);
    cursor: pointer;
}

