.normal-svg {
  cursor: default;
  background-color: #F0F6FB !important;
  overflow: auto;
  /* border: 1px solid lightgray;*/
  border-radius: 3px;
  background-image: linear-gradient(90deg, #eee 10%, rgba(0, 0, 0, 0) 10%), linear-gradient(#eee 10%, rgba(0, 0, 0, 0) 10%);
  background-size: 10px 10px;
  margin-right: 200px;
  height: 100%;
}
.grab-svg {
  cursor: grab;
  background-color: #F0F6FB !important;
  overflow: auto;
  /* border: 1px solid lightgray;*/
  border-radius: 3px;
  background-image: linear-gradient(90deg, #eee 10%, rgba(0, 0, 0, 0) 10%), linear-gradient(#eee 10%, rgba(0, 0, 0, 0) 10%);
  background-size: 10px 10px;
  margin-right: 200px;
  height: 100%;
}
.grab-svg:active {
  cursor: grabbing;
}
.body {
  margin: 0;
}
#isc_dataflow_editor {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
  width: 100%;
}
.el-loading-mask.is-fullscreen {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}
.foreignObject {
  /* 解决foreignObject默认遮挡溢出部分导致的形变 */
  overflow: inherit;
  animation-duration: 1s;
  animation-fill-mode: both;
  animation-name: fadeInLeft;
}
.side {
  position: absolute;
  width: 0;
  height: 0;
  margin-top: 15%;
  left: 3px;
  z-index: 3;
  cursor: pointer;
  /* background-color: #fff; */
}
.right-tool-bar {
  display: flex;
  /* width: 500px; */
  position: absolute;
  height: 100%;
  right: 0;
  top: 0;
  /* border-left: 1px solid #ccc; */
  /* background: #fff; */
  flex-direction: row;
}
.selected-type .all_resources .resource-each .resources:hover {
  transform: translateY(-4px);
  background: #F5F5F5;
  border-radius: 8px;
  box-shadow: 0 6px 24px rgba(38, 38, 38, 0.06);
}
.selected-type .groups .resources_group:hover {
  background: #F5F5F5;
  border-radius: 2px;
}
.selected-type .groups .resources_group:hover {
  color: #262626;
}
.selected-type {
  display: flex;
  /*padding-top: 20px;*/
  height: 100%;
}
.selected-type .groups {
  display: inline-block;
  vertical-align: top;
  flex-shrink: 0;
  margin-top: 10px;
  line-height: 32px;
  width: 100px;
  max-height: 100%;
  border-right: 1px solid var(--color-brand3-2, #e9edf0);
}
.selected-type .groups .resources_group {
  padding: 0 15px 2px 15px;
  font-size: 14px;
  font-weight: 400;
  text-align: left;
  color: #8c8c8c;
  cursor: pointer;
}
.selected-type .all_resources {
  overflow-y: auto;
  display: flex;
  flex-wrap: wrap;
  padding: 0 20px 14px;
  width: 100%;
}
.selected-type .all_resources .resource-each {
  text-align: left;
}
.selected-type .all_resources .resource-each .resource-title {
  color: var(--color-text1-7, #8b8b8b);
  /* color: #8b8b8b; */
  font-size: 14px;
  margin: 10px 10px 10px 15px;
}
.selected-type .all_resources .resource-each .resources {
  /* display: flex;*/
  width: 150px;
  height: 80px;
  text-align: left;
  background-color: hsla(0, 0%, 100%, 0.9);
  /*transition: background-color 0.2s;*/
  word-break: break-all;
  align-items: center;
  cursor: pointer;
  border: 1px solid var(--color-brand3-2, #e9edf0);
  border-radius: 8px;
  margin: 10px 10px 10px 15px;
  display: inline-flex;
  -webkit-box-align: center;
  transition: transform 0.218s ease-in, -webkit-transform 0.218s ease-in;
}
.selected-type .all_resources .resource-each .resources .resource-icon {
  padding-left: 4px;
}
.selected-type .all_resources .resource-each .resources .icon {
  font-size: 24px;
}
.selected-type .all_resources .resource-each .resources .resource-label {
  padding-left: 5px;
}
.selected-type .all_resources .resource-each .resources .resource-label .resource {
  font-size: 14px;
  font-weight: 600;
}
.selected-type .all_resources .resource-each .resources .resource-label .desc {
  font-size: 8px;
  color: #999;
}
